import React, { Component } from 'react';
import {Select, Button, Tabs, Tooltip, Alert} from 'antd';
import {commonControlles, allControlles} from '../Controlles';
import Formula from './formula';
import Outlink from './outlink';
import {AiMainItem, AiAllItem} from './AiMap.js';
import $ from 'jquery';
const TabPane = Tabs.TabPane;

class TextProps extends Component {
		constructor(props) {
				super(props);
				this.owner = this.props.owner;
				
				this.state = {
						ischanged: false,
						exprShowFlag: false,
						outLinkShowFlag: false,
						
					  "name": "",//标识符，数据存储字段名，可为空
					  "title": "单行文本",//标题，不为空
					  "alias": "",//显示别名
					  "desc": "",//描述，可为空
					  "len": 100,//字节数
					  "kind": "text",//类型，可选值为普通文本、电话号码、手机号码、邮政编号、电子邮件、身份证号
					  "defaultType": "custom",//默认值，可选值为custom、expr、outlink
					  "defaultValue": "",
					  "required": false,//必填标识
					  "noDup": false,//不可重复标识
					  "noDupAll": false,//全局不可重复标识
					  "readonly":false,//只读标识
					  "hidden":false,//隐藏标识
					  "asTitle":false,//作为单据标题标识
					  "isSearch": false,//查询字段
					  "link":"",//超链接地址
					  "type": "text",
					  "width": "",//宽度
					  "className": "",//控件样式
					  "hoverClassName": "",//鼠标悬浮样式
					  "onblur": "",
					  "onchange": "",
					  "onclick": "",
					  "ondblclick": "",
					  "onfocus": "",
					  "onkeydown": "",
					  "onkeyup": "",
					  "onlosecapture": "",
					  "onmousedown": "",
					  "onmouseenter": "",
					  "onmouseleave": "",
					  "onmouseout": "",
					  "onmouseup": "",
					  "aiItems": [],
					  "mergeEqual": false,
					  "group": false,
					  "isview": false,//数据管理视图不可见
				}
				
				this.state = {...this.state, ...this.props.def};
				
				this.commonControllesIndex = commonControlles.filter(control => control.name === this.state.type);
			  this.allControllesIndex = allControlles.filter(control => control.name === this.state.type);
			  
			  this.owner.comfirmControlProp = this.comfirmControlProp.bind(this);				
		}		
		
		componentWillReceiveProps(nextProps){
				let state = {...this.state, ...nextProps.def, ischanged: false};
				this.setState(state);
		}		
		
		setControlProp(key, value){
				if(key === "defaultType"){
						this.setState({[key]: value, "defaultValue": ""});
				}else{
						this.setState({[key]: value});
				}
				this.setState({ischanged: true});
				if(key === "type"){
						window.setTimeout(() => this.comfirmControlProp(), 0);						
				}				
		}
		
		comfirmControlProp(){
				if(this.state.ischanged){
						this.setState({ischanged: false});
						let state = {...this.state};
						delete state["ischanged"];
						delete state["exprShowFlag"];
						delete state["outLinkShowFlag"];
						this.owner.sysCurCellMeta(state);
				}
				if(this.props.parent_list !== ""){
						this.owner.setState({curSubField: null, curSubFieldIndex: 0});	
				}					
		}

		renderExpr(){
				const me = this;
				const data = {
							value: '',
							formId: me.props.formId,
							forms: '',
							logic: false,
							receiver(expr, vexpr) {
									me.setState({defaultValue: vexpr, exprShowFlag: false});
									me.setState({ischanged: true});
							},
							cancel() {
									me.setState({exprShowFlag: false});
							}
				}					
				if(this.state.defaultValue !== ''){
						$.ajaxSettings.async = false
						$.post('/general/appbuilder/web/appdesign/appform/convertfunc', {formId: this.props.formId, expr: this.state.defaultValue}, (res) => {
								if(res.status === 'ok'){
										data.value = res.data ? res.data: ''																																		
								}
						})
						$.ajaxSettings.async = true
						return <Formula formulaData={data} />						
				}else{																		
						return <Formula formulaData={data} />					
				}			
		}
				
		render(){
				const {commonControllesIndex, allControllesIndex} = this;
				const me = this;
				
				return (
						<div className="config-panel">
								{this.props.parent_list !== "" ? <Button className="ant-btn save-btn" onClick={this.comfirmControlProp.bind(this)}>确定</Button> : null}
								<Tabs defaultActiveKey="1" size='small' tabBarGutter='0'>
										<TabPane tab="基本属性" key="1">
												<div>														
									 					<div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>名称</label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" placeholder="请输入控件名称" className="config-text-len ant-input" value={this.state.title} onChange={e => this.setControlProp('title', e.target.value)}/>
									              </span>
									            </div>
									          </div>													
									 					<div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>显示别名</label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" placeholder="请输入显示别名" className="config-text-len ant-input" value={this.state.alias} onChange={e => this.setControlProp('alias', e.target.value)}/>
									              </span>
									            </div>
									          </div>									          
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>控件类型</label>
									              <span className="ant-input-wrapper config-typename">
									              		{
									              				commonControllesIndex && commonControllesIndex.length > 0  ?
																				<Select value={[this.state.type]} onChange={value => this.setControlProp('type', value)} size="small" style={{"width":"120px"}}>
																						{
																								commonControlles.map(control => <Select.Option value={control.name} key={control.name}>{control.label}</Select.Option>)
																						}
																				</Select> :
																				<span className="ant-input-wrapper config-typename">
																						{allControllesIndex[0]["label"]}
																				</span>
																		}
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>宽度</label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									                <input type="text" placeholder="最长不能超过所在单元格宽度" className="config-text-len ant-input" value={this.state.width} onChange={e => this.setControlProp('width', e.target.value)}/>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>字符长度</label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									                <input type="text" placeholder="默认最长为100个字符" className="config-text-len ant-input" value={this.state.len} onChange={e => this.setControlProp('len', e.target.value)}/>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>描述</label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									                <textarea type="textarea" placeholder="请输入控件描述" className="config-text-desc ant-input" value={this.state.desc} onChange={e => this.setControlProp('desc', e.target.value)}></textarea>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>类型</label>
									            </div>
									            <div className="ant-form-item-control">
																	<Select size="small" value={[this.state.kind]} onChange={value => this.setControlProp('kind', value)}>
																			<Select.Option value="text" key="text">普通文本</Select.Option>
																			<Select.Option value="phone" key="phone">手机号码</Select.Option>
																			<Select.Option value="tele" key="tele">电话号码</Select.Option>
																			<Select.Option value="postcode" key="postcode">邮政编码</Select.Option>
																			<Select.Option value="email" key="email">电子邮件</Select.Option>
																			<Select.Option value="idnumber" key="idnumber">身份证</Select.Option>
																	</Select>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>默认值</label>
									            </div>
									            <div className="ant-form-item-control has-success">
																	<Select size="small" value={[this.state.defaultType]} onChange={value => this.setControlProp('defaultType', value)} style={{width: 100}}>
																			<Select.Option value="custom" key="custom">自定义</Select.Option>
																			<Select.Option value="expression" key="expression">表达式</Select.Option>
																			<Select.Option value="outlink" key="outlink">关联提取</Select.Option>
																	</Select>
																	{
																			this.state.defaultType === "custom" ? <input type="text" placeholder="请输入控件默认值" className="config-text-len ant-input" value={this.state.defaultValue} onChange={e => this.setControlProp('defaultValue', e.target.value)}/> : this.state.defaultType === "expression" ? <div><Button size="small" onClick={()=>this.setState({exprShowFlag: true})}>定义表达式</Button></div> : <div><Button size="small" onClick={()=>this.setState({outLinkShowFlag: true})}>定义关联提取</Button></div>
																	}
																	{
																			this.state.defaultType === "expression" && this.state.exprShowFlag ? this.renderExpr() : null
																	}
																	{this.state.defaultType === "outlink" && this.state.outLinkShowFlag ? <Outlink {...this.props} owner={this}  updateOutlink={(def) => this.setState({defaultValue: def})}/> : null}
									            </div>
									          </div>
									          {
									          	0 ?
									          <div className="ant-row ant-form-item">
										            <div className="ant-form-item-label">
										              	<label>自动识别规则</label>
										            </div>
										          	<div className="ant-form-item-control has-success">
																		<Select size="small" value={this.state.aiItems} onChange={value => this.setControlProp('aiItems', value)} style={{width: 180}} mode="multiple">
																				{this.props.parent_list !== "" ? AiAllItem : AiMainItem}
																		</Select>
										            </div>
									          </div> : null
									        	}
									          <div className="ant-row ant-form-item last-ant-form-item">
									            <div className="ant-form-item-control has-success">
									              <label className="ant-checkbox-wrapper">
									                  <input type="checkbox" className="config-text-required" checked={this.state.required} onChange={e => this.setControlProp('required', e.target.checked)}/>
									                <span>必填</span>
									              </label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <label className="ant-checkbox-wrapper">
									                  <input type="checkbox" className="config-text-dup" checked={this.state.noDup} onChange={e => this.setControlProp('noDup', e.target.checked)}/>
									                <span>不可重复</span>
									              </label>
									            </div>
									            {this.props.parent_list !== "" ? 
									            <div className="ant-form-item-control has-success">
									              <label className="ant-checkbox-wrapper">
									                  <input type="checkbox" className="config-text-dup" checked={this.state.noDupAll} onChange={e => this.setControlProp('noDupAll', e.target.checked)}/>
									                <span>全体不可重复</span>
									              </label>
									            </div> : null
									          	}									            
									            <div className="ant-form-item-control has-success">
									              <label className="ant-checkbox-wrapper">
									                  <input type="checkbox" className="config-text-readonly" checked={this.state.readonly} onChange={e => this.setControlProp('readonly', e.target.checked)}/>
									                <span>只读</span>
									              </label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <label className="ant-checkbox-wrapper">
									                  <input type="checkbox" className="config-text-hidden" checked={this.state.hidden} onChange={e => this.setControlProp('hidden', e.target.checked)}/>
									                <span>隐藏</span>
									              </label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <label className="ant-checkbox-wrapper">
									                  <input type="checkbox" className="config-text-issearch" checked={this.state.isSearch} onChange={e => this.setControlProp('isSearch', e.target.checked)}/>
									                <span>是否是查询字段</span>
									              </label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <label className="ant-checkbox-wrapper">
									                  <input type="checkbox" className="config-text-asTitle" checked={this.state.asTitle} onChange={e => this.setControlProp('asTitle', e.target.checked)}/>
									                <span>是否作为标题</span>
									              </label>
									            </div>
									            {
									            	this.props.parent_list !== "" ? (
											            <div className="ant-form-item-control has-success">
											              <label className="ant-checkbox-wrapper">
											                  <input type="checkbox" className="config-text-mergeEqual" checked={this.state.mergeEqual} onChange={e => this.setControlProp('mergeEqual', e.target.checked)}/>
											                <span>合并相同值</span>
											              </label>
											            </div>										            									            	
									            	) : null
									            }
									            {
									            	this.props.parent_list !== "" ? (
																	<div className="ant-form-item-control has-success">
											              <label className="ant-checkbox-wrapper">
											                  <input type="checkbox" className="config-text-group" checked={this.state.group} onChange={e => this.setControlProp('group', e.target.checked)}/>
											                <span>分组</span>
											              </label>
											            </div>											            									            	
									            	) : null
									            }		
									            {this.props.parent_list === "" ? 
											            <div className="ant-form-item-control has-success">
											              <label className="ant-checkbox-wrapper">
											                  <input type="checkbox" className="config-text-isview" checked={this.state.isview} onChange={e => this.setControlProp('isview', e.target.checked)}/>
											                <span>是否在数据管理隐藏</span>
											              </label>
											            </div> : null
									        		}	
									        		</div>							
												</div>
										</TabPane>
										<TabPane tab="样式" key="2">
												<div>
														<div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>输入框样式</label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" placeholder="请输入样式名称" className="config-text-classname ant-input" value={this.state.className} onChange={e => this.setControlProp('className', e.target.value)}/>
									              </span>
									            </div>
									          </div>	
														<div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label>输入框鼠标悬停样式</label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" placeholder="请输入样式名称" className="config-text-hoverclassName ant-input" value={this.state.hoverClassName} onChange={e => this.setControlProp('hoverClassName', e.target.value)}/>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									          	<Alert message="自定义样式文件名称及存放路径为：/static/modules/appbuilder/appcenter/appform/{formNo}/addon.css, 其中{formNo}代表表单编号。假如表单编号为：frm01，那么自定义样式文件为：/static/modules/appbuilder/appcenter/appform/frm01/addon.css" type="info" style={{'word-break': 'break-all'}}/>	
									          </div>						          									
												</div>
										</TabPane>
										<TabPane tab="事件" key="3">
												<div>
														<div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="在对象失去输入焦点时触发">onblur</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="在对象失去输入焦点时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onblur} onChange={e => this.setControlProp('onblur', e.target.value)}/>
									              </span>
									            </div>
									          </div>	
														<div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当对象或选中区的内容改变时触发">onchange</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当对象或选中区的内容改变时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onchange} onChange={e => this.setControlProp('onchange', e.target.value)}/>
									              </span>
									            </div>
									          </div>		
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当对象或选中区的内容改变时触发">onclick</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当对象或选中区的内容改变时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onclick} onChange={e => this.setControlProp('onclick', e.target.value)}/>
									              </span>
									            </div>
									          </div>	
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当用户双击对象时触发">ondblclick</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当用户双击对象时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.ondblclick} onChange={e => this.setControlProp('ondblclick', e.target.value)}/>
									              </span>
									            </div>
									          </div>				
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当对象获得焦点时触发">onfocus</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当对象获得焦点时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onfocus} onChange={e => this.setControlProp('onfocus', e.target.value)}/>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当用户按下键盘按键时触发">onkeydown</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当用户按下键盘按键时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onkeydown} onChange={e => this.setControlProp('onkeydown', e.target.value)}/>
									              </span>
									            </div>
									          </div>	
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当用户释放键盘按键时触发">onkeyup</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当用户释放键盘按键时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onkeyup} onChange={e => this.setControlProp('onkeyup', e.target.value)}/>
									              </span>
									            </div>
									          </div>	
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当对象失去鼠标捕捉时触发">onlosecapture</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当对象失去鼠标捕捉时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onlosecapture} onChange={e => this.setControlProp('onlosecapture', e.target.value)}/>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当用户用任何鼠标按钮单击对象时触发">onmousedown</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当用户用任何鼠标按钮单击对象时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onmousedown} onChange={e => this.setControlProp('onmousedown', e.target.value)}/>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当用户将鼠标指针移动到对象内时触发">onmouseenter</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当用户将鼠标指针移动到对象内时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onmouseenter} onChange={e => this.setControlProp('onmouseenter', e.target.value)}/>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当用户将鼠标指针移出对象边界时触发">onmouseleave</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当用户将鼠标指针移出对象边界时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onmouseleave} onChange={e => this.setControlProp('onmouseleave', e.target.value)}/>
									              </span>
									            </div>
									          </div>			
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当用户将鼠标指针移出对象边界时触发">onmouseout</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当用户将鼠标指针移出对象边界时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onmouseout} onChange={e => this.setControlProp('onmouseout', e.target.value)}/>
									              </span>
									            </div>
									          </div>    
									          <div className="ant-row ant-form-item">
									            <div className="ant-form-item-label">
									              <label><Tooltip title="当用户在鼠标位于对象之上时释放鼠标按钮时触发">onmouseup</Tooltip></label>
									            </div>
									            <div className="ant-form-item-control has-success">
									              <span className="ant-input-wrapper">
									              <input type="text" hint="当用户在鼠标位于对象之上时释放鼠标按钮时触发" placeholder="请输入事件处理函数名称" className="config-text-len ant-input" value={this.state.onmouseup} onChange={e => this.setControlProp('onmouseup', e.target.value)}/>
									              </span>
									            </div>
									          </div>
									          <div className="ant-row ant-form-item">
									          	<Alert message="自定义脚本文件名称及存放路径为：/static/modules/appbuilder/appcenter/appform/{formNo}/addon.js, 其中{formNo}代表表单编号。假如表单编号为：frm01，那么自定义脚本文件为：/static/modules/appbuilder/appcenter/appform/frm01/addon.js" type="info" style={{'word-break': 'break-all'}}/>	
									          </div>									             										          									
												</div>
										</TabPane>
								</Tabs>
						</div>
				);
		}
}

export default TextProps;